// 车辆基本信息
export default{
	data(){
		return{
			gc_certificate:[],
		}
	},
	onLoad(option) {
		let certificate = JSON.parse(decodeURIComponent(option.json));
		this.gc_certificate = certificate[0].gc_certificate;
		//console.log(this.gc_certificate)
	}
}
